@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap');

.mt {
    margin-top: 20px;
}

.mr {
    margin-right: 20px;
}

.mb {
    margin-bottom: 20px;
}

.ml {
    margin-left: 20px;
}

.app {
    position: relative;
}

:root {
    font-size: 16px;
    @media screen and (max-width: 700px) {
        font-size: 12px;
    }
    @media screen and (max-width: 500px) {
        font-size: 8px;
    }
}

body {
    font-family: 'JetBrains Mono', monospace;
    padding-bottom: 60px;
    @media screen and (max-width: 700px) {
        padding-bottom: 30px;
    }
}

body * {
    font-family: inherit;
}

.title {
    max-width: 766px;
    text-align: center;
    font-size: 1.8rem;
    color: black;
    margin: 0 auto;
    line-height: 2.2rem;

    @media screen and (max-width: 400px) {
        max-width: 300px;
    }
}

header {
    padding: 60px 0;

    @media screen and (max-width: 700px) {
        padding: 20px 0;
    }
}

.container {
    max-width: 1120px;
    margin: 0 auto;
    padding: 0 20px;
    @media screen and (max-width: 400px) {
        padding: 0 10px;
    }
}

.grid-container {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 20px;

    @media screen and (max-width: 700px) {
        grid-gap: 10px;
    }
    @media screen and (max-width: 400px) {
        grid-gap: 8px;
    }
}

.card {
    height: fit-content;
    position: relative;

    @media screen and (min-width: 650px) {
        &:hover {
            .wrapper {
                opacity: 1;
            }
        }
    }

    img {
        width: 100%;
        height: fit-content !important;
        object-fit: cover;
    }
}

.wrapper {
    top: 0;
    left: 0;
    position: absolute;
    opacity: 0;
    height: 100%;
    width: 100%;
    background: rgba(255, 255, 255, 0.7);
    transition: 0.8s ease all;
}

.description {
    font-size: 1rem;
    line-height: 1.4rem;
    color: #000000;
    text-align: center;
    margin: auto 0;
    position: absolute;
    top: 50%;
    left: 0px;
    width: 100%;
    transform: translateY(-50%);
    padding: 0 20px;

    @media screen and (max-width: 650px) {
        padding: 0 10px;
    }
}

.overlay {
    top: 0;
    left: 0;
    position: fixed;
    background: #000000;
    height: 100vh;
    width: 100%;
}

.overlay-container {
    width: 100%;
    height: 100%;
    position: relative;

    > div {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        padding: 50px 0 30px 0;

        @media screen and (max-width: 650px) {
            height: fit-content;
        }
    }

    img {
        width: 100%;
        max-height: 92%;
        object-fit: contain;
    }

    .image-title {
        background: none;
        text-align: center;
        font-size: 0.8rem;
        color: #ffffff;
        line-height: 1.2rem;
        letter-spacing: 0.05rem;
        padding: 10px;
    }
}

.close-button {
    position: absolute;
    right: 20px;
    top: 20px;
    z-index: 20;
    background: none;
    border: none;
    padding: 0;

    svg {
        height: 30px;
        width: 30px;

        path {
            fill: #ffffff;
        }
    }
}
